<template>
  <div class="newDetail">
    <c-title :text="'详情'"></c-title>
    <h4>{{ dataInfo.title }}</h4>
    <div class="time">
      <span class="author">{{ dataInfo.author }}</span>
      <span class="time-created-at">{{
        dataInfo.updated_time ? dataInfo.updated_time : dataInfo.publish_time
      }}</span>
    </div>
    <div class="content">
      <img :src="dataInfo.thumbnail_y_2x" alt="" />
      <div
        v-html="dataInfo.cnt_html"
        id="con"
        @click="handleHtml($event)"
      ></div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      dataInfo: [],
    };
  },
  activated() {
    console.log(this.$route.params);
    this.getData();
  },
  methods: {
    async getData() {
      let { data, result, msg } = await $http.get(
        "apitest/getNbaNews",
        { news_id: this.$route.params.id },
        "获取中"
      );
      if (result) {
        this.dataInfo = data;
        console.log(JSON.parse(this.dataInfo.cnt_attr[0].object));
      } else {
        this.$toast(msg);
      }
    },
  },
};
</script>
<style lang="scss" scoped>
.newDetail {
  padding: 0 0.625rem;
  background: #fff;
  min-height: 100vh;
  .time {
    display: flex;
    align-items: center;
    text-align: left;
    line-height: 1.5625rem;
    height: 1.5625rem;
    margin-bottom: 1.25rem;
    margin-top: 0.8438rem;
  }
  .author {
    color: #3b3b4a;
    font-size: 0.875rem;
    margin-right: 0.625rem;
  }
  .time-created-at {
    color: #aaaab3;
    font-size: 0.8125rem;
  }
  .content {
    width: 100%;
    font-size: 16px;
    text-align: left;
    padding-top: 0.625rem;
    padding-bottom: 1.875rem;
    word-break: break-all;

    img {
      width: 100%;
    }

    li {
      list-style: inherit;
    }
  }
}
</style>
